import React, { Component } from 'react'
import ReactDOM from 'react-dom';
import style from './iframe.scss';

const Iframe = (props) => {
    let { state = {} } = props.location
    return ReactDOM.createPortal(
        <div className="warper">
            <div className="header">
                <a onClick={g.goBack} className="span">
                    <i className="i iconfont icon-jiantou-left"></i>
                </a>
                <h1 className="h1">{'外链'}</h1>
                <a className="spanc"></a>
            </div>
            {state.outlink ?
                <iframe className={style.iframe} src={state.outlink}></iframe> :
                <div className={style.nourl}>外部链接不存在</div>
            }
        </div>,
        document.getElementById('root')
    )
}

export default Iframe


